:root {
    --bg-glass-color-header: #ffffff80;
    --border-glass-color-header: #92002f61;
    --page-pad-large: 100px;
    --page-pad-medium: 70px;
    --page-pad-small: 20px;

    --content-pad-large: 60px;
    --content-pad-medium: 70px;
    --content-pad-small: 20px;
    --content-pad-xsmall: 12px;

    --text-color-primary: #000;
    --text-color-invert: #fff;
    --text-color-brand: #92002f;
    --text-color-error: #b32626;
    --border-color-primary: #000;
    --border-color-neutral: #000;
    --border-color-brand: #87002b;
    --border-color-error: #db5151;
    --bg-color: #fff;
    --bg-color-brand: #92002f;
    --bg-color-hover: #f0f0f0;
    --bg-color-hover-light: #f0f0f0;
    --bg-color-hover-brand: #92002ecc;
    --border-radius: 8px;
    --border-radius-input: 4px;

    --text-title-size-large: 60px;
    --text-title-lineheight-size-large: 68px;
    --text-subtitle-size-large: 32px;
    --text-p-size-large: 18px;

    --text-title-size-medium: 48px;
    --text-title-lineheight-size-medium: 60px;
    --text-subtitle-size-medium: 24px;
    --text-p-size-medium: 16px;

    --text-title-size-small: 30px;
    --text-title-lineheight-size-small: 38px;
    --text-subtitle-size-small: 20px;
    --text-p-size-small: 14px;
}

section.content {
    width: 100%;
    margin: 0 auto;
    padding: 0 var(--page-pad-small);
}

label {
    display: block;
    padding-bottom: 3px;
}

input,
textarea,
select {
    height: 32px;
    border-radius: var(--border-radius-input);
    border: 1px solid rgb(221, 219, 218);
    padding: 0 14px;
    width: 100%;
}

textarea {
    height: unset;
    padding: 14px;
}

textarea:focus,
input:focus,
select:focus {
    outline: none;
}

.jf-error input {
    border: 1px solid var(--border-color-error);
}

.text-brand {
    color: var(--text-color-brand);
}

span.jf-error {
    color: var(--text-color-error);
    font-size: 13px;
}

.flex {
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
}

.flex-sb {
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
}

.flex-align-center {
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}

.flex-justify-center {
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
}

.flex-direction-row {
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-direction: row;
    flex-direction: row;
}

.flex-direction-column {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
}

.flex-wrap {
    -ms-flex-wrap: wrap;
    flex-wrap: wrap
}

.jf-hide {
    display: none;
}

.jf-p-left_xsmall {
    padding-left: 8px;
}

.jf-p-left_small {
    padding-left: 12px;
}

.jf-p-right_small {
    padding-right: 12px;
}

.jf-p-vertical_small {
    padding-top: 12px;
    padding-bottom: 12px;
}

.jf-p-vertical_medium {
    padding-top: 16px;
    padding-bottom: 16px;
}

.jf-p-vertical_large {
    padding-top: 24px;
    padding-bottom: 24px;
}

.jf-m-vertical_small {
    margin-top: 12px;
    margin-bottom: 12px;
}

.jf-m-vertical_medium {
    margin-top: 16px;
    margin-bottom: 16px;
}

.jf-m-vertical_large {
    margin-top: 24px;
    margin-bottom: 24px;
}

.jf-m-vertical_xlarge {
    margin-top: 32px;
    margin-bottom: 32px;
}

.jf-morphism-brand {
    background: rgba(146, 0, 47, 0.6);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
}

.jf-morphism-white {
    background: rgba(255, 255, 255, 0.6);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(6px);
    -webkit-backdrop-filter: blur(6px);
    border: 1px solid rgba(255, 255, 255, 0.2);
}

.loader {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #ffffff9e;
    z-index: 1;
}

.spinner {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 20px;
    height: 20px;
    color: var(--text-color-brand);
    background:
        linear-gradient(currentColor 0 0),
        linear-gradient(currentColor 0 0),
        linear-gradient(currentColor 0 0),
        linear-gradient(currentColor 0 0);
    background-size: 16px 16px;
    background-repeat: no-repeat;
    animation: loading 1.5s infinite cubic-bezier(0.3, 1, 0, 1);
}

.page {
    display: block;
    position: relative;
    color: var(--text-color-primary);
    width: 100%;
    height: 100vh;
    padding: 0 var(--page-pad-small);
    padding-top: 100px !important;
}

.main-background {
    background-image: url('./assets/images/building.jpg');
    background-size: cover;
    background-position: center bottom;
    background-repeat: no-repeat;
}
.page .title {
    font-size: var(--text-title-size-small);
    line-height: var(--text-title-lineheight-size-small);
    font-weight: 900;
}

.page .role {
    font-size: var(--text-subtitle-size-small);
    font-weight: 100;
    margin: 8px 0;
}

.page .content {
    font-size: var(--text-p-size-large);
}

.page .glass-pads {
    width: 100%;
    padding: 40px 20px;
    font-size: 16px;
}

@media only screen and (min-width: 48em) {
    section.content {
        padding: 0 var(--page-pad-medium);
    }
    .page {
        padding: 0 var(--page-pad-medium);
    }
    .main-background {
        padding: 0 var(--page-pad-medium);
        background-size: contain;
    }

    .page .title {
        font-size: var(--text-title-size-medium);
        line-height: var(--text-title-lineheight-size-medium);
        font-weight: 900;
    }

    .page .subtitle {
        font-size: var(--text-subtitle-size-medium);
        font-weight: 100;
    }

    .page .content {
        font-size: var(--text-p-size-medium);
    }

    .page .glass-pads {
        padding: 60px 40px;
    }
}

@media only screen and (min-width: 78rem) {
    section.content {
        padding: 0 var(--page-pad-large);
    }
    .page {
        padding: 0 var(--page-pad-large);
    }

    .page .title {
        font-size: var(--text-title-size-large);
        line-height: var(--text-title-lineheight-size-large);
        font-weight: 900;
    }

    .page .subtitle {
        font-size: var(--text-subtitle-size-large);
        font-weight: 100;
        margin: 8px 0;
    }

    .page .content {
        font-size: var(--text-p-size-large);
    }
}

@keyframes loading {
    0% {
        background-position: 0 0, 100% 0, 100% 100%, 0 100%
    }

    33% {
        background-position: 0 0, 100% 0, 100% 100%, 0 100%;
        width: 40px;
        height: 40px
    }

    66% {
        background-position: 100% 0, 100% 100%, 0 100%, 0 0;
        width: 40px;
        height: 40px
    }

    100% {
        background-position: 100% 0, 100% 100%, 0 100%, 0 0
    }
}